@import url(https://fonts.googleapis.com/css?family=Orbitron&display=swap);
@import url(https://fonts.googleapis.com/css?family=Orbitron&display=swap);
body,html{margin:0;height:100%;overflow:hidden;background-image:-webkit-gradient(linear, left bottom, left top, from(rgba(0,0,0,0.6)), to(rgba(0,0,0,0.7))),url("/bg.jpg");background-image:linear-gradient(0deg, rgba(0,0,0,0.6), rgba(0,0,0,0.7)),url("/bg.jpg");background-repeat:no-repeat;background-size:cover;background-attachment:fixed;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}@media (max-height: 200px){body,html{overflow:scroll}}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.drum-pad{background-color:#1a1a1a;color:#333;width:99%;height:99%;margin:auto;font-family:'Orbitron';font-size:12px;display:flex;flex-direction:row;justify-content:space-around;align-items:center;box-shadow:0px 0px 3px 3px red;cursor:pointer}@media (max-height: 600px) and (max-width: 600px){.drum-pad{font-size:5px}}#Q-active{-webkit-transform:translateY(3px);transform:translateY(3px);background:radial-gradient(#d98c8c, #bf4040)}#W-active{-webkit-transform:translateY(3px);transform:translateY(3px);background:radial-gradient(#d9ba8c, #bf8c40)}#E-active{-webkit-transform:translateY(3px);transform:translateY(3px);background:radial-gradient(#c9d98c, #a6bf40)}#A-active{-webkit-transform:translateY(3px);transform:translateY(3px);background:radial-gradient(#9cd98c, #59bf40)}#S-active{-webkit-transform:translateY(3px);transform:translateY(3px);background:radial-gradient(#8cd9ab, #40bf73)}#D-active{-webkit-transform:translateY(3px);transform:translateY(3px);background:radial-gradient(#8cd9d9, #40bfbf)}#Z-active{-webkit-transform:translateY(3px);transform:translateY(3px);background:radial-gradient(#8cabd9, #4073bf)}#X-active{-webkit-transform:translateY(3px);transform:translateY(3px);background:radial-gradient(#9c8cd9, #5940bf)}#C-active{-webkit-transform:translateY(3px);transform:translateY(3px);background:radial-gradient(#c98cd9, #a640bf)}

#drum-machine{background-color:#0d0d0d;display:flex;flex-direction:column-reverse;height:90vh;width:60vw;margin:20px auto;padding:10px;font-family:'Orbitron';border:1px solid black;box-shadow:inset 0 0 2px 4px #1a1a1a,inset 0 0 0 8px gray;border-radius:10px;font-size:14px}@media (max-height: 600px) and (max-width: 600px){#drum-machine{font-size:8px}}#drum-pads{display:grid;grid-template-columns:auto auto auto;grid-template-rows:auto auto auto;width:90%;height:75%;margin:auto;padding:5px}#display-box{border:1px solid gray;background-color:black;display:flex;flex-direction:column;align-items:center;width:40%;height:20%;margin:7px auto;border-radius:10px}#display-box #logo{width:50%;display:flex;flex-direction:row;align-items:center;justify-content:space-around;color:white;font-size:1em}@media (max-width: 600px){#display-box #logo{font-size:0.9em}}#display-box #display{background-color:#2e842e;width:70%;height:50%;display:flex;margin-top:3%;box-shadow:0 0 5px 5px #333}#display-box #display #audio-name{color:#1a1a1a;margin:auto}@media (max-height: 600px) and (max-width: 600px){#display-box #display #audio-name{font-size:0.7em}}

